@font-face {
  font-family: 'Arial Narrow';
  src: url('./fonts/arial_narrow_bold_italic.eot');
  src: url('./fonts/arial_narrow_bold_italic.eot?#iefix') format('embedded-opentype'),
  url('./fonts/arial_narrow_bold_italic.woff2') format('woff2'),
  url('./fonts/arial_narrow_bold_italic.woff') format('woff'),
  url('./fonts/arial_narrow_bold_italic.svg#arial_narrowbold_italic') format('svg');
  font-weight: 600;
  font-style: italic;

}

@font-face {
  font-family: 'Arial Narrow';
